body {
  margin: 0;
  font-family: Roboto, sans-serif;
}

.header {
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.logo {
  width: 40px;
  height: 40px;
  display: block;
}

.nav-list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav-item {
  margin-left: 20px;
}

.nav-item:first-child {
  color: #00acdc;
  margin-left: 0;
}

.nav-link {
  text-transform: uppercase;
  white-space: nowrap;
  color: #060b35;
  cursor: pointer;
  align-items: center;
  height: 60px;
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
  transition: all .3s;
  display: flex;
  position: relative;
}

.nav-link:hover, .nav-link.is-active {
  color: #00acdc;
}

.nav-link.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 37px;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

.cards {
  --column-count: 1;
  grid-template-columns: repeat(var(--column-count), 200px);
  gap: 44px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width <= 488px) {
  .cards {
    --column-count: 1;
  }
}

@media (width >= 488px) {
  .cards {
    --column-count: 2;
  }
}

@media (width >= 768px) {
  .cards {
    --column-count: 3;
  }
}

@media (width >= 1024px) {
  .cards {
    --column-count: 4;
  }
}

.cards {
  color: #616070;
  justify-content: center;
  font-family: Roboto, sans-serif;
  font-size: 10px;
  line-height: 14px;
}

.card {
  box-sizing: border-box;
  background: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  height: 410px;
  padding: 32px 16px 16px;
  transition: transform .3s, width .3s, height .3s;
}

.card__image {
  width: 160px;
  height: 134px;
  margin: 0 auto 40px;
  display: block;
}

.card__caption {
  color: #060b35;
  margin: 0 0 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__code {
  margin: 0 0 16px;
}

.card__reviews {
  justify-content: space-between;
  align-items: flex-end;
  margin-bottom: 24px;
  display: flex;
}

.card__review {
  color: #060b35;
  margin: 0;
}

.card__price {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 16px;
  display: flex;
}

.card__price-text {
  margin: 0;
  font-size: 12px;
  line-height: 18px;
}

.card__price-amount {
  color: #060b35;
  margin: 0;
  font-size: 16px;
  font-weight: bold;
  line-height: 18px;
}

.card__button {
  box-sizing: border-box;
  color: #fff;
  text-transform: uppercase;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  height: 40px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  display: flex;
}

.card__button:hover {
  color: #00acdc;
  background-color: #fff;
}

.card:hover {
  transform: scale(1.2);
}

.card:hover .card__caption {
  color: #34568b;
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars__star:last-child {
  margin-right: 0;
}

.stars--1 .stars__star:nth-child(-n+1), .stars--2 .stars__star:nth-child(-n+2), .stars--3 .stars__star:nth-child(-n+3), .stars--4 .stars__star:nth-child(-n+4), .stars--5 .stars__star:nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}
